// Menu list
.menu-list {
  width: 224px;
  .menu-item {
    margin-left: 0;
  }
}

.menu-body {
  overflow-y: auto;
}
// Menu item
.menu-item {
  width: 100%;
  justify-content: flex-start;
  border-radius: none;
  font-weight: 600;
  padding: 11px 16px;
  line-height: 1.8rem;
  font-weight: 500;
  &.separate {
    border-top: 1px solid rgba(22, 24, 35, 0.12);
  }
  &:hover {
    background-color: rgba(22, 24, 35, 0.03);
  }
}

// Menu header
.header {
  position: relative;
  height: 50px;
  margin-top: -8px;
  flex-shrink: 0;
}

.back-btn {
  width: 50px;
  height: 100%;
  background-color: transparent;
  cursor: pointer;
}

.header-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
