.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-height: min((100vh - 96px) - 60px, 734px);
  min-height: 100px;
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 12%) 0px 2px 12px;
  box-sizing: border-box;
  border-radius: 8px;
  overflow: hidden auto;
  padding-top: 8px;
  padding-bottom: 8px;
}
